import Home from '../views/Home'
import NotFound from '../views/NotFound'
import Fences from '../views/Fences';
import SafeNet from '../views/SafeNet';
import StairDoor from '../views/StairDoor';

const appRoutes = [
    {
        path: '/',
        component: Home,
        exact: true
    },
    {
        path: '/cercos',
        component: Fences,
        exact: true
    },
    {
        path: '/redes',
        component: SafeNet,
        exact: true
    },
    {
        path: '/puertas',
        component: StairDoor,
        exact: true
    },
    {
        component: NotFound
    },
];

export default appRoutes;