import React, { Component } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEnvelope, faMobileAlt, faUser } from '@fortawesome/free-solid-svg-icons'
import * as emailjs from 'emailjs-com';
import $ from 'jquery';


const ConfirmationModal = () => (
    <div className="modal" tabIndex="-1" role="dialog">
        <div className="modal-dialog" role="document">
            <div className="modal-content">
                <div className="modal-header">
                    <h5 className="modal-title">Gracias por escribirnos</h5>
                    <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div className="modal-body">
                    <p>Nos pondremos en contacto a la brevedad!</p>
                </div>
                <div className="modal-footer">
                    <button type="button" className="btn btn-primary" data-dismiss="modal">Aceptar</button>
                </div>
            </div>
        </div>
    </div>
);

export default class HomeSectionContact extends Component {
    constructor(props) {
        super(props);

        this.state = {
            alertMessage: '',
            userEmail: '',
            userPhone: '',
            message: ''
        }
    }

    componentDidMount() {
        emailjs.init('user_R79oL4iueRBczyECzZUTy');
    }

    handleUserName (e) {
        let userName = e.target.value;
        this.setState(prevState => ({
            ...prevState,
            userName
        }));
    }
    
    handleUserEmail (e) {
        let userEmail = e.target.value;
        this.setState(prevState => ({
            ...prevState,
            userEmail
        }));
    }

    handleUserPhone (e) {
        let userPhone = e.target.value;
        this.setState(prevState => ({
            ...prevState,
            userPhone
        }));
    }

    handleMessage (e) {
        let message = e.target.value;
        this.setState(prevState => ({
            ...prevState,
            message
        }));
    }

    isValidContactForm() {
        let { message, userEmail } = this.state;
        if (!message) {
            this.setState({
                alertMessage: 'No has ingresado tu consulta.'
            });
            return false;
        }
        if (!userEmail) {
            this.setState({
                alertMessage: 'Ingresá tu e-mail así podemos contactarnos con vos.'
            });
            return false;
        }
        this.setState({
            alertMessage: ''
        });
        return true;
    }

    onContactFormSubmit = (e) => {
        e.preventDefault();
        let { message, userPhone, userEmail, userName } = this.state;

        if(this.isValidContactForm()){
            let templateParams = {
                "reply_to": userEmail,
                "from_name": userName,
                "to_name": "Sabri",
                "message_html": message,
                "phone_number": userPhone
             }
             
             let service_id = "default_service";
             let template_id = "template_zLSIMDJi";
             
             
            emailjs.send(service_id, template_id, templateParams)
                .then(response => {
                    this._resetContactForm();
                }, err => {
                    console.log('FAILED...', err);
                });

            $('.modal').modal();
        }
    }

    _resetContactForm() {
        this.setState(prevState => ({
            ...prevState,
            message: '',
            userEmail: '',
            userName: '',
            userPhone: ''
        }));
    }

    _shouldShowAlert() {
        const { alertMessage } = this.state;
        if (alertMessage) {
            return (
                <div className="alert alert-warning" role="alert">
                    {alertMessage}
                </div>
            );
        }
    }

    render () {
        return (
            <div className="section">
                {this._shouldShowAlert()}
                <h2 className="title text-primary--dark text-center">Contacto</h2>

                <div className="container">
                    <form onSubmit={this.onContactFormSubmit}>

                        <div className="form-group">
                            <label htmlFor="userName">Nombre:</label>
                            <div className="input-group">
                                <div className="input-group-prepend">
                                    <div className="input-group-text">
                                        <FontAwesomeIcon icon={faUser} />
                                    </div>
                                </div>
                                <input
                                    type="text"
                                    className="form-control"
                                    id="userName"
                                    aria-describedby="nameHelp"
                                    onChange={this.handleUserName.bind(this)} />
                            </div>
                        </div>

                        <div className="form-group">
                            <label htmlFor="userEmail">e-mail:</label>
                            <div className="input-group">
                                <div className="input-group-prepend">
                                    <div className="input-group-text">
                                        <FontAwesomeIcon icon={faEnvelope} />
                                    </div>
                                </div>
                                <input
                                    type="email"
                                    className="form-control"
                                    id="userEmail"
                                    aria-describedby="emailHelp"
                                    onChange={this.handleUserEmail.bind(this)} />
                            </div>
                        </div>

                        <div className="form-group">
                            <label htmlFor="userPhone">Teléfono:</label>
                            <div className="input-group">
                                <div className="input-group-prepend">
                                    <div className="input-group-text">
                                        <FontAwesomeIcon icon={faMobileAlt} />
                                    </div>
                                </div>
                                <input
                                    type="tel"
                                    className="form-control"
                                    id="userPhone"
                                    onChange={this.handleUserPhone.bind(this)} />
                            </div>
                        </div>
                        <div className="form-group">
                            <label htmlFor="message">Mensaje: </label>
                            <textarea
                                className="form-control"
                                placeholder="Ingresá tu mensaje"
                                id="message"
                                onChange={this.handleMessage.bind(this)} />
                        </div>
                        <button type="submit" className="btn btn-primary">Enviar</button>
                    </form>

                </div>

                <ConfirmationModal />
            </div>
        );
    }
}
