import React, { Component } from 'react'

const CarouselIndicator = ({ numberOfSlides, carouselRef }) => {
    const dataTarget = `#${carouselRef}`;
    const indicators = [];
    for (let i = 0; i < numberOfSlides; i++) {
        indicators.push(
            <li data-target={dataTarget} data-slide-to={i} className={i === 0 ? 'active' : ''} key={i}></li>
        );
    }
    return (
        <ol className="carousel-indicators">
            {indicators}
        </ol>
    );
}

const CarouselImages = ({ images }) => {
    return images.map((imgage, key) => {
        const props = {
            className: `carousel-item ${key === 0 ? 'active' : ''}`
        }
        return (
            <div {...props} key={key}>
                <img className="d-block w-100" src={imgage} alt={imgage} />
            </div>
        );
    });
}

export default class Carousel extends Component {
    render() {
        const { carouselImages, carouselRef } = this.props;
        return (
            <div id={carouselRef} className="carousel slide" data-ride="carousel">
                <CarouselIndicator numberOfSlides={carouselImages.length} carouselRef={carouselRef} />
                <div className="carousel-inner">
                    <CarouselImages images={carouselImages} />
                </div>
                <a className="carousel-control-prev" href={`#${carouselRef}`} role="button" data-slide="prev">
                    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span className="sr-only">Previous</span>
                </a>
                <a className="carousel-control-next" href={`#${carouselRef}`} role="button" data-slide="next">
                    <span className="carousel-control-next-icon" aria-hidden="true"></span>
                    <span className="sr-only">Next</span>
                </a>
            </div>
        );
    }
}
