import {debounce} from 'lodash';
import $ from 'jquery';


const NowUiKit = {
  $navbar: null,
  transparent: true,
  scroll_distance: 0,
  misc: {
    navbar_menu_visible: 0
  },

  checkScrollForTransparentNavbar(){

    if ($(document).scrollTop() > this.scroll_distance) {
      if (this.transparent) {
        this.transparent = false;
        $('.navbar[color-on-scroll]').removeClass('navbar-transparent');
      }
    } else {
      if (!this.transparent) {
        this.transparent = true;
        $('.navbar[color-on-scroll]').addClass('navbar-transparent');
      }
    }
  },

  initSliders() {
    // Sliders for demo purpose in refine cards section
    // let slider = document.getElementById('sliderRegular');

    // noUiSlider.create(slider, {
    //   start: 40,
    //   connect: [true, false],
    //   range: {
    //     min: 0,
    //     max: 100
    //   }
    // });

    // var slider2 = document.getElementById('sliderDouble');

    // noUiSlider.create(slider2, {
    //   start: [20, 60],
    //   connect: true,
    //   range: {
    //     min: 0,
    //     max: 100
    //   }
    // });
  },

  startNowUI() {
    $(document).ready(() => {
      //  Activate the Tooltips
      $('[data-toggle="tooltip"], [rel="tooltip"]').tooltip();

      // Activate Popovers and set color for popovers
      $('[data-toggle="popover"]').each(() => {
        let color_class = $(this).data('color');
        $(this).popover({
          template: '<div class="popover popover-' + color_class + '" role="tooltip"><div class="arrow"></div><h3 class="popover-header"></h3><div class="popover-body"></div></div>'
        });
      });

      this.$navbar = $('.navbar[color-on-scroll]');
      this.scroll_distance = this.$navbar.attr('color-on-scroll') || 500;

      // Check if we have the class "navbar-color-on-scroll" then add the function to remove the class "navbar-transparent" so it will transform to a plain color.

      if ($('.navbar[color-on-scroll]').length != 0) {
        this.checkScrollForTransparentNavbar();
        $(window).on('scroll', debounce(this.checkScrollForTransparentNavbar.bind(this), 17))
      }

      $('.form-control').on("focus", () => {
        $(this).parent('.input-group').addClass("input-group-focus");
      }).on("blur", () => {
        $(this).parent(".input-group").removeClass("input-group-focus");
      });

      // Activate bootstrapSwitch
      $('.bootstrap-switch').each(() => {
        let $this = $(this);
        let data_on_label = $this.data('on-label') || '';
        let data_off_label = $this.data('off-label') || '';

        $this.bootstrapSwitch({
          onText: data_on_label,
          offText: data_off_label
        });
      });

      // Activate Carousel
      // $('.carousel').carousel({
      //   interval: 4000
      // });

      $('.date-picker').each(() => {
        $(this).datepicker({
          templates: {
            leftArrow: '<i class="now-ui-icons arrows-1_minimal-left"></i>',
            rightArrow: '<i class="now-ui-icons arrows-1_minimal-right"></i>'
          }
        }).on('show', () => {
          $('.datepicker').addClass('open');

          let datepicker_color = $(this).data('datepicker-color');
          if (datepicker_color.length != 0) {
            $('.datepicker').addClass('datepicker-' + datepicker_color + '');
          }
        }).on('hide', () => {
          $('.datepicker').removeClass('open');
        });
      });


    });

    $(document).on('click', '.navbar-toggler', () => {
      let $toggle = $(this);

      if (this.misc.navbar_menu_visible == 1) {
        $('html').removeClass('nav-open');
        this.misc.navbar_menu_visible = 0;
        $('#bodyClick').remove();
        setTimeout(() => {
          $toggle.removeClass('toggled');
        }, 550);
      } else {
        setTimeout(() => {
          $toggle.addClass('toggled');
        }, 580);
        let div = '<div id="bodyClick"></div>';
        $(div).appendTo('body').click(() => {
          $('html').removeClass('nav-open');
          this.misc.navbar_menu_visible = 0;
          setTimeout(() => {
            $toggle.removeClass('toggled');
            $('#bodyClick').remove();
          }, 550);
        });

        $('html').addClass('nav-open');
        this.misc.navbar_menu_visible = 1;
      }
    });
  }
}

export default NowUiKit;