import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBars, faTimes } from '@fortawesome/free-solid-svg-icons'
import { faFacebook } from '@fortawesome/free-brands-svg-icons';


import Logo from '../assets/images/logo-white.png';


export default class Navbar extends Component {

    constructor(props){
        super(props);

        this.state = {
            isCollapsed: true
        }
    }

    _toggleCollapse = () => {
        this.setState((prevState) => ({
            isCollapsed: !prevState.isCollapsed
        }));
    }

    _buildToggleIcon () {
        const {isCollapsed} = this.state;
        return isCollapsed ? <FontAwesomeIcon icon={faBars} /> : <FontAwesomeIcon icon={faTimes} />;
    }

    render() {
        const { isTransparent = false } = this.props;
        const navProps = {
            className: `navbar navbar-expand-lg bg-primary fixed-top ${isTransparent ? 'navbar-transparent' : ''}`,
            'color-on-scroll': '300',
        }
        return (
            <nav {...navProps}>
                <div className="container">
                    <div className="navbar-translate">
                        <NavLink className="navbar-brand" to="/" rel="tooltip" title="" data-placement="bottom">
                            <img src={Logo} className="navbar-logo" />
                            <strong>SeguRed</strong>
                        </NavLink>
                        <button 
                            className="navbar-toggler" 
                            type="button" data-toggle="collapse" 
                            data-target="#navigation" 
                            aria-controls="navigation-index" 
                            aria-expanded="false" 
                            aria-label="Toggle navigation"
                            onClick={this._toggleCollapse}>
                            {this._buildToggleIcon()}
                        </button>
                    </div>
                    <div className="collapse navbar-collapse justify-content-end has-image navbar-navigation" id="navigation">
                        <ul className="navbar-nav">
                            <li className="nav-item">
                                <a className="nav-link" href="/#productos">Productos</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="/#contacto">Contacto</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" rel="tooltip" title="" data-placement="bottom" href="https://www.facebook.com/seguredMendoza/" target="_blank" data-original-title="Seguinos en Facebook">
                                    <span className="navbar-icon" ><FontAwesomeIcon size="2x" icon={faFacebook} /></span>
                                    <p className="d-lg-none d-xl-none"> Facebook</p>
                                </a>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        );
    }
}